import React from 'react'
import {Parallax} from 'react-scroll-parallax'
import './style.css'
import Mint from './img/mint.jfif'
import Berry from './img/berry.jfif'

const First = () => {
    return (
        <div>
            <div className="fill">

            </div>
            <div className="container">
                <Parallax y={[120,0]}>
                    <img src={Mint} alt=''/>
                </Parallax>
                <Parallax y={[0,0]} className="world">
                    <img src={Berry} alt=''/>
                </Parallax>
            </div>
            <div className="fill">

            </div>
        </div>
    )
}

export default First
